// Open Sans
@font-face {
    font-family: 'Gotham_pro';
    src: url('../fonts/GothamPro.eot');
    src: url('../fonts/GothamPro.eot?#iefix') format('embedded-opentype'),
         url('../fonts/GothamPro.woff') format('woff'),
         url('../fonts/GothamPro.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;

}

